    background-color: inherit;
    pointer-events: none;
    position: absolute;
    z-index: 1;
    top: -1px;
    left: -1px;
    bottom: -1px;
    right: -1px;
    border-radius: inherit;
    border: 0 solid;
    border-color: inherit;
    opacity: 0.3;
    -webkit-animation: xue-ui-wave-animation 0.3s ease-in-out forwards;
            animation: xue-ui-wave-animation 0.3s ease-in-out forwards;
    -ms-flex-negative: 0;
        flex-shrink: 0; }

@-webkit-keyframes xue-ui-wave-animation {
  to {
    top: -6px;
    left: -6px;
    bottom: -6px;
    right: -6px;
    border-width: 6px;
    opacity: 0; } }

@keyframes xue-ui-wave-animation {
  to {
